.cards-wrapper {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 3.5rem;
    padding-top: 5%;
    margin: 0 auto;
    width: max-content;
}

.cardProduk {
    --bg-filter-opacity: 0.1;
    background-image: linear-gradient(rgba(0, 0, 0, var(--bg-filter-opacity)), rgba(0, 0, 0, var(--bg-filter-opacity))), var(--bg-img);
    height: 15.5em;
    width: 11em;
    font-size: 1.5em;
    color: white;
    border: none;
    border-radius: 10px;
    padding: 1em;
    display: flex;
    align-items: flex-end;
    transition: all, var(--transition-time);
    position: relative;
    overflow: hidden;
    text-decoration: none;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
    -webkit-transition: all, var(--transition-time);
    -moz-transition: all, var(--transition-time);
    -ms-transition: all, var(--transition-time);
    -o-transition: all, var(--transition-time);
}

#card1,
#card2,
#card3,
#card4,
#card5,
#card6,
#card7 {
    background-size: cover;
    background-position: center;
    height: 15.5em;
    width: 11em;
}

#card1 {
    --bg-img: url("../assets/ARTTIMES/1.png");
    --bg-filter-opacity: 0.1;
    background-image: linear-gradient(rgba(0, 0, 0, var(--bg-filter-opacity)), rgba(0, 0, 0, var(--bg-filter-opacity))), var(--bg-img);
