.cardProduk:hover {
    --bg-filter-opacity: 0.4;
    background-image: linear-gradient(rgba(0, 0, 0, var(--bg-filter-opacity)), rgba(0, 0, 0, var(--bg-filter-opacity))), var(--bg-img);
    transform: scale(1.1);
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -ms-transform: scale(1.1);
    -o-transform: scale(1.1);
}

.cardProduk:hover .date {
    opacity: 1;
    font-weight: 700;
    font-size: 16px;
}

.cardProduk .tags .tag {
    font-size: 0.75em;
    background: #1abc9c85;
    color: #fff;
    border-radius: 0.3rem;
    padding: 0 0.5em;
    margin-top: 16.5em;
    margin-right: 7em;
    line-height: 1.5em;
    transform: scaleX(1);
    transform-origin: left;
    transition: all, var(--transition-time) ease-out;
    -webkit-transition: all, var(--transition-time) ease-out;
    -moz-transition: all, var(--transition-time) ease-out;
    -ms-transition: all, var(--transition-time) ease-out;
    -o-transition: all, var(--transition-time) ease-out;
    -webkit-transform: scaleX(1);
    -moz-transform: scaleX(1);
    -ms-transform: scaleX(1);
    -o-transform: scaleX(1);
}

.cardProduk:hover .tags .tag {
    transform: scaleX(0);
    -webkit-transform: scaleX(0);
    -moz-transform: scaleX(0);
    -ms-transform: scaleX(0);
    -o-transform: scaleX(0);
}

.cardProduk .date {
    position: absolute;
    color: #fff;
    top: 0;
    right: 0;
    font-size: 0.65em;
    padding: 1em;
    line-height: 1em;
    opacity: .8;
}

.cardProduk:after {
    content: '';
    background-image: url('../assets/ARTTIMES/art times 1.png');
    background-position: center;
    background-size: cover;
    height: 15.5em;
    width: 11em;
    bottom: 0;
    right: -100%;
    border-radius: 8px;
    position: absolute;
    z-index: -5;
    transition: all, var(--transition-time);
    transition-timing-function: ease-in-out;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}

.cardProduk:hover:before,
.cardProduk:hover:after {
    right: 0;
}


#produk {
    width: 100%;
    height: 300vh;
    display: flex;
    background-color: var(--black-color);
    justify-content: center;
}

.lamanProduk {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 5% 10% 0;
    background-color: var(--content);
    border-radius: 14px;
    -webkit-border-radius: 14px;
    -moz-border-radius: 14px;
    -ms-border-radius: 14px;
    -o-border-radius: 14px;
}

.titleB {
    width: 100%;
    margin-top: 7%;
    text-align: center;
}

.titleB h1 {
    letter-spacing: 2px;
    color: #1abc9c;
    font-weight: 700;
    font-size: 40px;
}

.titleB h2 {
    cursor: none;
    margin-top: 0;
    letter-spacing: -7px;
    color: var(--sky-color);
    max-width: 700px;
    font-weight: bolder;
    display: inline-block;
}

.titleB p {
    margin-top: 2%;
    color: var(--sky-color);
    font-weight: bolder;
}

.layerCardProduk {
    width: 90%;
    height: 250vh;
    margin: auto;
    border-radius: 12px;
    background-color: var(--card-content);
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    -ms-border-radius: 12px;
    -o-border-radius: 12px;
}